






















import Vue from 'vue';
import { remote } from 'electron';
const win = remote.getCurrentWindow();

export default Vue.extend({
  name: 'App',
  data: () => ({
      maxicon: 'mdi-window-maximize',
  }),

  methods: {

    quit() {
      win.close();
    },

    minimize() {
      win.minimize();
    },

    maximize() {
      if (!win.isMaximized()) {
        win.maximize();
        this.maxicon = 'mdi-window-restore';
      } else {
        win.unmaximize();
        this.maxicon = 'mdi-window-maximize';
      }
    },
  },
});


